<template>
  <div class="hello">
    <textarea @keyup="getText" id="editor" name cols="30" rows="10">
      # H1
      ## H2
      [link]()

      inline code

      ``` code block ```
      - list item
      > blockquote
      !image
      **bolded text**
    </textarea>
    <div id="preview">
      <div v-for="(str, idx) in editorTextArr" :key="idx">
        <h1 v-if="(/^#\s/g).test(str)">{{str.replace(/#\s/g, '')}}</h1>
        <h2 v-if="(/^##\s/g).test(str)">{{str.replace(/##\s/g, '')}}</h2>
        <li v-if="(/^[-+*]\s/g).test(str)">{{str.replace(/[-+*]\s/g, '')}}</li>
        <a href="" v-if="(/^(\[\w+\])/g).test(str)">{{str.replace(/(\[(\w+)\])(\(\))/g, '$2')}}</a>
        <code v-if="(/^(```)(.*)(```)$/g).test(str)">{{str.replace(/`/g, '')}}</code>
        <blockquote v-if="(/^>\s/g).test(str)">{{str.replace(/>\s/g, '')}}</blockquote>
        <img v-if="(/^!/g).test(str)" src="" alt="">
        <strong v-if="(/^(\*\*)(.*)(\*\*)/g).test(str)">{{str.replace(/\*/g, '')}}</strong>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      editorText: null,
      editorTextArr: [],
    };
  },
  methods: {
    getText() {
      return this.editorTextArr = this.$el.children[0].value.split('\n').map(str => str.trim())
    }
  },
  mounted() {
    this.editorText = this.$el.textContent
    this.editorTextArr = this.$el.textContent.split('\n').map(str => str.trim())
  }
};
</script>

